html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  font-weight: 400;
  background-color: var(--color-white);
}

img {
  width: 250px;
  max-width: 100%;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
  padding: 0;
}

.is-outer {
  position: relative !important; // stylelint-disable-line declaration-no-important
  left: -9999px !important; // stylelint-disable-line declaration-no-important
}

.site-container {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  
}

.is-hidden {
  display: none !important; // stylelint-disable-line declaration-no-important
}

.for-mobile {
  display: none;

  @include mobile {
    display: block;
  }
}

.error {
  color: red;  // stylelint-disable-line declaration-no-important

}

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
}

.container {
  width: 80%;
  margin: 0 auto;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.footer {
  margin-top: auto;
}

.main{
  padding-top:120px;
}